//
export function calcularTotal(cantidad, plazo) {
    //Cantidades
    // 0 - 1000 = 25%
    //1001 - 5000 = 20%
    //5001 - 10000 = 15%
    // +10000 = 10%
    let interesCantidad = (cantidad>=10001) ? (cantidad*0.10) : 
                        (cantidad>= 5001) ? (cantidad*0.15) :
                        (cantidad>=1001)  ? (cantidad*0.20) : (cantidad*0.25) 
    console.log('Interes * Cantidad',interesCantidad);

    let interesPlazo;
    //3 - 5%
    //6 - 10%
    //12 - 15%
    //24 - 20%
    switch (plazo) {
        case 3:
            interesPlazo = (cantidad * 0.05)
            break;
        case 6:
            interesPlazo = (cantidad * 0.10)
            break;
        case 12:
            interesPlazo = (cantidad * 0.15)
        break;
        case 24:
            interesPlazo = (cantidad * 0.20)
        break;   
        default:
            break;
    }

    console.log('Interes * Plazo',interesPlazo);

    return cantidad += interesCantidad + interesPlazo
}