import {React, Fragment, useState } from "react";
import{calcularTotal} from '../helpers'


const Formulario = (props)=>{
    const {cantidad,guardarCantidad,plazo, guardarPlazo,guardarTotal, guardarCargando} = props; 
    //Definir state
    const [error, guardarError] = useState(false)

    //Cuando el usuario hace submit
    const calcularPrestamo = (e)=>{
        e.preventDefault();
        //Validar
        if((cantidad.length || plazo.length ) <=0 || cantidad ===0){
            console.log('vacios');
            guardarError(true)
            return;
        }
        //Eliminar el error previo
        guardarError(false)
        //Habilitar el spinner
        guardarCargando(true)

        setTimeout(() => {
            //realiza la cotizacion
            const total = calcularTotal(cantidad, plazo)
            console.log(total);
            //una vez calculado, guarda el total
            guardarTotal(total);      
            //deshabilitar spinner
            guardarCargando(false)
        }, 3000);
      
     

    }
    return( 
    <Fragment>
        <form onSubmit={calcularPrestamo}>
            <div className="row">
                <div>
                    <label>Cantidad Prestamo</label>
                    <input className="u-full-width" onChange={e => guardarCantidad(Number(e.target.value))} type="number" placeholder="Ejemplo: 3000"></input>
                </div>
                <div>
                    <label> Plazo para pagar </label>
                    <select className="u-full-width" onChange={e=> guardarPlazo(Number(e.target.value))}>
                        <option value="">Seleccionar</option>
                        <option value="3">3 meses</option>
                        <option value="6">6 meses</option>
                        <option value="12">12 meses</option>
                        <option value="24">24 meses</option>
                    </select>
                </div>
                <div>
                    <input type="submit" value="Calclular" className="button-primary u-full-width"/>
                </div>
            </div>
        </form>

        {(error)?<p className="error">Todos los campos son obligatorios</p> : null }
        
    </Fragment>
    )
}


export default Formulario;